<template>
  <div id="app">
    <router-link to="/index">首页</router-link>
    <router-link to="/freecourse">免费课程</router-link>
    <router-link to="/seniorcourse">收费课程</router-link>
    <router-link to="/shoppingcar">购物车</router-link>
    <router-link to="/payment">我的订单</router-link>
    <router-link to="/test">测试</router-link>
    <div v-if="this.$store.state.token">
      <h1>欢迎{{this.$store.state.username}}</h1>
      <a @click="layout">注销</a>
    </div>
    <div v-else>
       <router-link to="/login">登录</router-link>
       <router-link to="/register">注册</router-link>
    </div>

    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  methods:{
    layout(){
      this.$store.commit("deleteToken")
      this.$router.push({path:"/index"})
    }
}
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



