<template>
  <div>
      <table>

        <tr>

          <th>书名</th>
          <th>图片</th>
          <th>价格</th>
        </tr>
        <tr v-for="item in FreeCourseList">
          <td>
            <input type="checkbox" @click="HandleCheckClick(item)" v-bind:checked="item.select"/>
            {{item.name}}
          </td>
          <td><img :src= "'../../static/'+item.img"></td>
          <td>{{item.price}}</td>
        </tr>
      </table>
    <div>
      <router-link to="/shoppingcar"><input type="button" value="加入到购物车"></router-link>
    </div>
  

  </div>
</template>

<script>

    export default {
      name: "freecourse",
      data() {
        return {
          FreeCourseList: "",
          checkall: true,

        }
      },
      mounted() {
        this.GetFreeCourseList()
      },
      methods: {

        GetFreeCourseList() {
          var that = this
          this.$axios.request({
            url: this.$store.state.apiList.freecourse,
            method: "GET",
          }).then(function (ret) {
            if (ret.data.code === 1000) {
              that.FreeCourseList = ret.data.data
            } else {
              alert("获取课程失败")
            }

          }).catch(function () {
            alert("发生错误")
          })
        },

        HandleCheckClick(item) {

          item.select = !item.select
          if (item.select) {
            this.$store.state.add_to_car_list.push(item.name)
          } else {
            this.$store.state.add_to_car_list.pop(item.name)
          }
        },


      },




    }
</script>

<style scoped>

</style>



