import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/Index'
import FreeCourse from '@/components/FreeCourse'
import SeniorCourse from '@/components/SeniorCourse'
import Login from '@/components/Login'
import ShoppingCar from '@/components/ShoppingCar'
import Register from '@/components/Register'
import Payment from '@/components/Payment'
import Test from '@/components/Test'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/index',
      name: 'Index',
      component: Index
    },
    {
      path: '/test',
      name: 'Test',
      component: Test
    },
    {
      path: '/freecourse',
      name: 'FreeCourse',
      component: FreeCourse
    },
    {
      path: '/seniorcourse',
      name: 'SeniorCourse',
      component: SeniorCourse,
      meta: {
        require: true
      }
    },
    {
      path: '/shoppingcar',
      name: 'ShoppingCar',
      component: ShoppingCar,
      meta:{
        require:true
      }

    },
        {
      path: '/payment',
      name: 'Payment',
      component: Payment,
      meta:{
        require:true
      }

    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/register',
      name: 'Register',
      component: Register
    },
  ],
  mode:"history"
})



