<template>
    <div>
      <table>

        <tr>

          <th>书名</th>
          <th>图片</th>
          <th>价格</th>
        </tr>
        <tr v-for="item in PaymentList">
          <td>
            <!--<input type="checkbox" @click="HandleCheckClick(item)" v-bind:checked="item.select"/>-->
            {{item.name}}
          </td>
          <td><img :src= "'../../static/'+item.img"></td>
          <td>{{item.price}}</td>
        </tr>
      </table>
      <div>
        <p>实付款:{{AutuallyPrice}}</p>
      <input id="button" type="button" value="提交订单" @click="Alipay">
    </div>
      <form :action="url" method="post">
        <input type="submit" value="提交">
      </form>
    </div>
</template>

<script>
    export default {
      name: "payment",
      data() {
        return {
          PaymentList:"",
          AutuallyPrice:105,
          url:""
        }
      },
      mounted() {
        this.function()

      },
      methods: {
        function(){
          var that = this
          setTimeout(function(){
            that.GetPayment()
          },100)
        },
        GetPayment() {
          var that = this
          this.$axios.request({
            url: this.$store.state.apiList.payment,
            method: "GET",
            params: {
              token: this.$store.state.token
            },
          }).then(function (ret) {
            that.PaymentList = ret.data.data
            // console.log(ret)
          })
        },
        Alipay(){
          var that = this
          this.$axios.request({
            url:this.$store.state.apiList.alipay,
            method:"POST",
            params: {
              token: this.$store.state.token
            },
            headers:{
              'Content-Type': 'application/x-www-form-urlencoded',
              // 'Content-Type': 'application/json',
            },
            data:{
              price:this.AutuallyPrice
            }
          }).then(function(ret){
            console.log(that.url)
            that.url = ret.data.data
          })
        }
      }
    }
</script>

<style scoped>

</style>



