import Vue from "vue"
import Cookie from "vue-cookies"
import Vuex from "vuex"

Vue.use(Vuex)

export default new Vuex.Store({
  state:{
    username:Cookie.get("user"),
    token:Cookie.get("token"),
    add_to_car_list:[],
    add_to_payment_list:[],
    apiList:{
      auth:"http://127.0.0.1:8000/mysite/auth/",
      register:"http://127.0.0.1:8000/mysite/register/",
      freecourse:"http://127.0.0.1:8000/mysite/freecourse/",
      seniorcourse:"http://127.0.0.1:8000/mysite/seniorcourse/",
      shoppingcar:"http://127.0.0.1:8000/mysite/shoppingcar/",
      payment:"http://127.0.0.1:8000/mysite/payment/",
      alipay:"http://127.0.0.1:8000/mysite/alipay/"
      // auth:"http://47.106.153.44:8001/mysite/auth/",
      // register:"http://47.106.153.44:80001/mysite/register/",
      // freecourse:"http://47.106.153.44:8001/mysite/freecourse/",
      // seniorcourse:"http://47.106.153.44:8001/mysite/seniorcourse/",
      // shoppingcar:"http://47.106.153.44:8001/mysite/shoppingcar/",
      // payment:"http://47.106.153.44:8001/mysite/payment/",
      // alipay:"http://47.106.153.44:8001/mysite/alipay/"
    }
  },
  mutations:{
    saveToken(state,usertoken){
          state.username = usertoken.name
          state.token = usertoken.token
          Cookie.set("user",usertoken.name,"20min")
          Cookie.set("token",usertoken.token,"20min")
    },
    deleteToken(state){
          state.username = null
          state.token = null
          Cookie.remove("user")
          Cookie.remove("token")
    }
  }

})



