<template>
  <div>
    <div v-for="item in SeniorCourseList">
      <h1>书名：{{item.name}}</h1>
      <h1>价格：{{item.price}}</h1>
      <div>
        <img :src= "'../../static/'+item.img">
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "seniorcorse",
        data(){
          return{
            SeniorCourseList:""
          }
        },
        mounted(){
          this.GetSeniorCourseList()
        },
        methods:{
          GetSeniorCourseList(){
            var that = this
            this.$axios.request({
              url:this.$store.state.apiList.seniorcourse,
              method:"GET",
              params:{
                token:this.$store.state.token
              }
            }).then(function(ret){
              if(ret.data.code ===1000){
                that.SeniorCourseList = ret.data.data
              }else{
                  alert("获取课程失败")
              }

            }).catch(function(){
              alert("发生错误")
            })
          }
        }

    }
</script>

<style scoped>

</style>



