<template>
    <div>
      <table>

        <tr>

          <th>书名</th>
          <th>图片</th>
          <th>价格</th>
        </tr>
        <tr v-for="item in ShoppingCarList">
          <td>
            <input type="checkbox" @click="HandleCheckClick(item)" v-bind:checked="item.select"/>
            {{item.name}}
          </td>
          <td><img :src= "'../../static/'+item.img"></td>
          <td>{{item.price}}</td>
        </tr>
      </table>
      <div>
        <p>总价:{{TotalPrice}}</p>
      <router-link to="/payment"><input id="button" type="button" value="结算" v-on:click="AddPayment"></router-link>
    </div>
    </div>
</template>

<script>
    export default {
        name: "ShoppingCar",
        data(){
          return{
              ShoppingCarList:"",
            TotalPrice:0
          }
        },
      mounted(){
          this.AddShopping()
          // this.GetShopping()
      },
      methods:{
        AddShopping(){

              var that = this
              // console.log(this.$store.state.addbookslist)
              this.$axios.request({
              url:this.$store.state.apiList.shoppingcar,
              method:"POST",
              params:{
                token:this.$store.state.token
              },
              data:{
               books:this.$store.state.add_to_car_list
              }
            }).then(function(ret){
                that.GetShopping()
              // console.log("添加成功")
                  // that.ShoppingCarList = ret.data.data
                // console.log(that.ShoppingCarList)
              // console.log(ret)
              });
              this.$store.state.add_to_car_list = []




      },
        GetShopping(){
            var that = this
            this.$axios.request({
              url:this.$store.state.apiList.shoppingcar,
              method:"GET",
              params:{
                token:this.$store.state.token
              },
            }).then(function(ret){
              that.ShoppingCarList = ret.data.data
              // console.log(ret)
            })
        },
        HandleCheckClick(item) {

          item.select = !item.select
          if (item.select) {
            this.$store.state.add_to_payment_list.push(item.name)
          } else {
            this.$store.state.add_to_payment_list.pop(item.name)
          }
        },
        AddPayment() {

          var that = this
          // console.log(this.$store.state.addbookslist)
          this.$axios.request({
            url: this.$store.state.apiList.payment,
            method: "POST",
            params: {
              token: this.$store.state.token
            },
            data: {
              books: this.$store.state.add_to_payment_list
            }
          }).then(function (ret) {
            // that.GetPayment()
            // console.log("添加成功")
            // that.ShoppingCarList = ret.data.data
            // console.log(that.ShoppingCarList)
            // console.log(ret)
          });
          this.$store.state.add_to_payment_list = []


        },
        }
    }
</script>

<style scoped>
    #button{display:block; width:50px; height:40px; line-height: 40px; margin:0 auto; border:none; background-color:#41b883; color:#fff; font-size:16px; margin-bottom:5px;}

</style>



